<template>
  <div class="happysynth-play">
      <piano-keys @notedown="noteOn" @noteup="noteOff" octave="3"></piano-keys>
      <piano-keys @notedown="noteOn" @noteup="noteOff" octave="4"></piano-keys>
      <piano-keys @notedown="noteOn" @noteup="noteOff" octave="5"></piano-keys>
      <message-container>
          <message :is-shown="loadingMsgShown">
              <icon icon-type="loading" style="color:#28f"></icon>
              ...
          </message>
          <message :is-shown="loadSuccessMsgShown">
              <icon icon-type="check" style="color:#6b6"></icon>
              {{loadSuccessMsgText}}
          </message>
          <message :is-shown="loadFailMsgShown">
              <icon icon-type="warning" style="color:#f32"></icon>
              
          </message>
      </message-container>
  </div>
</template>

<script>
import PianoKeys from '../../components/happysynth/PianoKeys'
import sampler from '../../sampler'
import MessageContainer from '../../components/common/Message/MessageContainer'
import Message from '../../components/common/Message/Message'
import Icon from '../../components/common/Icon/Icon'
import {waveform} from '../../network/happysynth'
import { setPriority } from 'os'
export default {
    name:'HappySynthPlay',
    data(){
        return {
            loadingMsgShown:false,
            loadSuccessMsgShown:false,
            loadFailMsgShown:false,
            loadSuccessMsgText:"",
            sampler:null
        }
    },
    components:{
        PianoKeys,Message,MessageContainer,Icon
    },
    methods:{
        noteOn(pitch){
            // console.log(pitch)
            this.sampler.triggerAttack(pitch)
        },
        noteOff(pitch){
            this.sampler.triggerRelease(pitch)
        }
    },
    mounted(){
        Promise.all([
            new Promise(resolve=>{
                setTimeout(()=>{resolve()},100)
            }).then(()=>{
                this.loadingMsgShown=true
                return new Promise(resolve=>{
                    setTimeout(()=>{resolve()},1000)
                })
            }),
            waveform.get('')
        ]).then(res=>{
            // console.log(res)
            if(res[1].data.status===404){
                this.loadingMsgShown=false
                this.loadSuccessMsgText=''
                this.loadSuccessMsgShown=true
                setTimeout(()=>{this.loadSuccessMsgShown=false},1000)
                this.sampler= new sampler(
                    res[1].data.data,
                    ()=>{
                        console.log(`sample loaded? ${this.sampler.loaded}`)
                    }
                ).toMaster()
            }else{
                this.loadingMsgShown=false
                this.loadSuccessMsgText=''
                this.loadSuccessMsgShown=true
                setTimeout(()=>{this.loadSuccessMsgShown=false},1000)
                this.sampler= new sampler(
                    res[1].data.data,
                    ()=>{
                        console.log(`sample loaded? ${this.sampler.loaded}`)
                    }
                ).toMaster()                                
            }
        }).catch(err=>{
            console.log(err)
            this.loadingMsgShown=false
            this.loadFailMsgShown=true
            setTimeout(()=>{this.loadFailMsgShown=false},1000)
        })
    }
}
</script>

